<template>
	<view>
		<navbar title-color="#ffffff" background="#1b43c4" :iSimmersive="false" :placeholder="true" title="我的充值"></navbar>
		<form>
			<view style="margin: 0 auto; width: 92%;">
				<div class="rechange-title">
					<span>充值金额</span>
				</div>
				<div class="rechange-list">
				    <div
					class="rechange-item"
					:class="{active: item.checked}"
					v-for="(item, index) in moneyList"
					:key="index"
					@click="checkePrice(item)"
				    >
				        <span>{{ moneyList.length == index + 1 ? item.money : "￥" + item.money + "元" }}</span>
				     </div>
				</div>
				
				<div  v-if="moneyList.length > 0" class="rechange-title other-box" :class="{active: other}">
				    <span>其他金额</span>
				</div>
				
				<div class="money-input other-box" :class="{active: other}">
				    <input type="number" v-model="price" placeholder="请输入其他金额">
				</div>
			</view>
			
			<view class="order">
				<view class="title">
					<text>支付方式</text>
				</view>
				
				<view class="payment-box">
					<view 
						v-for="(item,index) in providerList" 
						:key="index"
						class="payment-item" 
						@click="selectPayment(item.id)"
					>
						<view><text class="iconfont pay" :id="item.id" :class="item.class"></text></view>
						<view :class="{activeColor:payment == item.id}">{{item.name}}</view>
						<view :class="{active:payment == item.id}"><text class="iconfont">&#xe631;</text></view>
					</view>
				</view>
				
			</view>
			
			<view class="form-box">
				<view class="form-submit">
					<view class="btn" :class="{ active: isActive }" @click="formSubmit">充值</view>
				</view>
			</view>
		</form>
	</view>
</template>

<script>
	import navbar from "@/components/navbar/navbar";
	import payment from '@/common/payment';
	export default {
		components: {
			navbar
		},
		data(){
			return {
				moneyList: [],
				other: false,
				price: "",
				isActive: false,
				payment: "wechat",
				providerList: []
			};
		},
		onLoad() {
			payment.getPaymentList("rechange").then(res=>{
				this.providerList = res;
			})
			
			this.$http.getRechangePrice().then((res)=>{
			  if(res.data.length > 0){
				this.moneyList = res.data;
				this.price = this.moneyList[0].money;
				this.other = false;
			  }else{
				this.other = true;
			  }
			});
		},
		onPageScroll(obj){
			this.scrollNum = obj.scrollTop;
		},
		methods: { 
			selectPayment(value){
				this.payment = value;
			},
			checkePrice(item){
			  this.moneyList.forEach(item=> {
				item.checked = false;
			  });
		
			  this.other = item.money == "其他";
			  this.price = item.money == "其他" ? '' : item.money;
		
			  item.checked = true;
			},
			formSubmit(){
				if(this.isActive){
					return ;
				}
				
				let price = parseFloat(this.price);
				if(price <= 0 || this.price == ''){
					this.$utils.msg("请输入要充值金额");
					return ;
				}
				
				this.isActive = true;
				
				this.$http.paymentWallet({
					payment: this.payment,
					source: payment.getPaymentType(),
					price: this.price,
					// #ifdef H5
					url: document.location.href
					// #endif
				}).then(res=>{
					if(res.status){
						payment.setPayType("rechange").crreateOrder(res.data);
					}else{
						this.$utils.msg(res.info);
					}
					this.isActive = false;
				}).catch(err=>{
					this.isActive = false;
					this.$utils.msg("连接网络出错，请稍后在试。");
				});
			}
		}
	}
</script>

